.risk-preference-container {
  margin-top: 40px;
  font-size: 30px;
}
.risk-preference-ul-label {
  display: flex;
  justify-content: space-between; 
  margin-left: 12%;
  margin-right: 17%;
  font-size: 22px;
}
.risk-preference-ul {
  display: flex;
  list-style: none;
  justify-content: center; 
}
.risk-preference-li {
  border: 3px solid black;
  border-radius: 5px;
  padding: 10px;
  line-height: 35px;
  width:60px;
  height: 60px;
  margin-right: 20px;
  cursor: pointer;
}
.risk-preference-continue {
  border: 3px solid black;
  border-radius: 5px;
  padding: 10px;
  line-height: 35px;
  height: 60px;
  cursor: pointer;
  background-color: #73d0ff;
  font-weight: bolder;
  font-size: 20px;
}
.mt-40 {
  margin-top: 40px;
  padding-left: 12%;
  padding-right: 12%;
}
.portfolio-label {
  display: flex;
  justify-content: space-between; 
}
.portfolio-rebalance {
  border: 3px solid black;
  border-radius: 5px;
  padding: 10px;
  line-height: 25px;
  height: 50px;
  cursor: pointer;
  background-color: #73d0ff;
  font-weight: bolder;
  font-size: 20px;
}
.wid-50-percent {
  width: 50%;
}
.wid-20-percent {
  table {
    width: 75%;
  }
  td {
    width: 20%;
  }
  span {
    width: 40%;
    display: inline-block;
  }
}
.input-table {
  margin-left: 5px;
  width:120px;
}
.input-table-2 {
  width:100%
}
.pie-icon {
  font-size: 40px;
  line-height: 55px;
  margin-right: 4px;
  color: blue;
}
.line-icon {
  font-size: 35px;
  line-height: 55px;
  margin-right: 4px;
  color: blue;
}
.pie-icon-container {
  margin-left: 20px;
  border: 3px solid black;
  width: 60px;
  height: 60px;
  border-radius: 5px;
  cursor: pointer;
}
.donut-container {
  width: 680px;
  height: 410px;
  text-align: center;
}